@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserratregular.woff2") format("woff2"), url("../fonts/montserratregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserratmedium.woff2") format("woff2"), url("../fonts/montserratmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserratsemibold.woff2") format("woff2"), url("../fonts/montserratsemibold.woff") format("woff");
  font-display: swap;
}
.page {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
}
.page__body {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.logo {
  display: flex;
  align-items: center;
  color: #333333;
  text-decoration: none;
  font-size: 28px;
  font-weight: 600;
}
@media (max-width: 1330px) {
  .logo {
    font-size: 16px;
  }
}
.logo img {
  margin: 0 20px 0 0;
}
@media (max-width: 1330px) {
  .logo img {
    width: 38px;
    height: 34px;
    margin: 0 14px 0 0;
  }
}
.button {
  padding: 17px 55px 16px 55px;
  font-size: 22px;
  line-height: 27px;
  font-weight: 500;
  color: #ffffff;
  background-color: #394573;
  border-radius: 40px;
  border: none;
  cursor: pointer;
}
@media (max-width: 1330px) {
  .button {
    padding: 18px 6px 14px 7px;
    font-size: 14px;
    line-height: 17px;
  }
}
.button:hover,
.button:focus,
.button:active {
  background-color: #50609e;
}
.button--special {
  padding: 17px 49px 16px 49px;
  background: linear-gradient(to right, #A0D2DB, #726DA8);
}
.button--special:hover,
.button--special:focus,
.button--special:active {
  background: linear-gradient(to left, #A0D2DB, #726DA8);
}
.container {
  max-width: 1480px;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  margin-right: auto;
  margin-left: auto;
}
@media (max-width: 1330px) {
  .container {
    padding: 0 20px;
    margin-right: 0;
    margin-left: 0;
  }
}
.main-header {
  padding: 40px 112px 32px 112px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1330px) {
  .main-header {
    padding: 32px 0 32px 0;
    position: relative;
  }
}
.main-header__toggle {
  display: none;
}
@media (max-width: 1330px) {
  .main-header__toggle {
    display: block;
    position: relative;
    width: 32px;
    height: 22px;
    border: none;
    background-color: transparent;
    cursor: pointer;
  }
}
@media (max-width: 1330px) {
  .main-header__toggle-line {
    position: absolute;
    left: 0;
    width: 32px;
    height: 2px;
    background-color: #726DA8;
    border-radius: 2px;
  }
}
@media (max-width: 1330px) {
  .main-header__toggle-line::before {
    content: "";
    position: absolute;
    top: -12px;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: #726DA8;
    border-radius: 2px;
  }
}
@media (max-width: 1330px) {
  .main-header__toggle-line::after {
    content: "";
    position: absolute;
    top: 10px;
    left: 0;
    width: 28px;
    height: 2px;
    background-color: #726DA8;
    border-radius: 2px;
  }
}
.main-nav {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1330px) {
  .main-nav {
    margin: 0 -20px 0 -20px;
    padding: 0 20px 0 20px;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 3;
    height: 100vh;
    justify-content: center;
    align-items: center;
    background-color: #394573;
  }
}
@media (max-width: 1330px) {
  .main-nav--closed {
    display: none;
  }
}
.main-nav__close-button {
  position: absolute;
  top: 30px;
  right: 32px;
  width: 24px;
  height: 24px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.main-nav__close-button:hover,
.main-nav__close-button:focus,
.main-nav__close-button:active {
  opacity: 80%;
}
.main-nav__close-button-line {
  position: absolute;
  top: -2px;
  right: 11px;
  width: 2px;
  height: 28px;
  background-color: #BEE7E8;
  transform: rotate(45deg);
  border-radius: 1px;
}
.main-nav__close-button-line::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 2px;
  height: 28px;
  background-color: #BEE7E8;
  transform: rotate(-90deg);
  border-radius: 1px;
}
.main-nav__close-button--closed {
  display: none;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  list-style-type: none;
}
.site-list {
  margin: 0 125px 0 0;
  font-size: 23px;
  line-height: 28px;
}
@media (max-width: 1330px) {
  .site-list {
    margin: 0;
    flex-direction: column;
    font-size: 24px;
    line-height: 29px;
    font-weight: 400;
  }
}
.site-list__item:not(:last-child) {
  margin: 0 40px 0 0;
}
@media (max-width: 1330px) {
  .site-list__item:not(:last-child) {
    margin: 0 0 14px 0;
  }
}
.site-list__item:hover,
.site-list__item:focus,
.site-list__item:active {
  opacity: 80%;
}
.site-list__item a {
  padding: 10px 0;
  text-decoration: none;
  color: #333333;
}
@media (max-width: 1330px) {
  .site-list__item a {
    color: #BEE7E8;
  }
}
@media (max-width: 1330px) {
  .social-list {
    display: none;
  }
}
.social-list__item:not(:last-child) {
  margin: 0 28px 0 0;
}
.social-list__link {
  display: flex;
  justify-content: center;
  align-items: center;
}
.social-list__link:hover,
.social-list__link:focus,
.social-list__link:active {
  opacity: 80%;
}
.main-container {
  flex-grow: 1;
}
.slider {
  min-height: 600px;
  position: relative;
  overflow: hidden;
}
@media (max-width: 1330px) {
  .slider {
    padding: 0 0 32px 0;
    min-height: 400px;
  }
}
.slider__line {
  display: flex;
  justify-content: stretch;
  transition: all 1s;
}
.slider__slide {
  padding: 60px 112px 15px 112px;
  position: relative;
  display: flex;
  background-color: #ffffff;
  background-image: url("../img/ellipse.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center 100%;
}
@media (max-width: 1330px) {
  .slider__slide {
    height: 377px;
    padding: 32px 0 0 20px;
    border-radius: 24px;
    background-image: none;
    background-color: #394573;
  }
}
.slider__slide--plan {
  background-image: url("../img/dog-background.png");
  background-repeat: no-repeat;
  background-size: cover;
  color: #394573;
}
@media (min-resolution: 2dppx) {
  .slider__slide--plan {
    background-image: url("../img/dog-background@2x.png");
  }
}
@media (max-width: 1330px) {
  .slider__slide--plan {
    background-image: url("../img/dog-background-mobile.png");
    background-position: center;
  }
}
@media (max-width: 1330px) and (min-resolution: 2dppx) {
  .slider__slide--plan {
    background-image: url("../img/dog-background-mobile@2x.png");
  }
}
.slider__content-wrapper {
  width: 61%;
}
@media (max-width: 1330px) {
  .slider__content-wrapper {
    width: 100%;
  }
}
.slider__slide-title {
  margin: 45px 0 24px 0;
  font-size: 50px;
  line-height: 61px;
  font-weight: 600;
  color: #BEE7E8;
}
@media (max-width: 1330px) {
  .slider__slide-title {
    margin: 0 0 18px 0;
    font-size: 24px;
    line-height: 29px;
  }
}
.slider__slide-title--plan {
  color: #394573;
}
.slider__slide-description {
  width: 90%;
  margin: 0 0 47px 0;
  font-size: 30px;
  line-height: 37px;
  color: #A0D2DB;
}
@media (max-width: 1330px) {
  .slider__slide-description {
    margin: 0 0 25px 0;
    font-size: 14px;
    line-height: 17px;
  }
}
.slider__slide-description--plan {
  width: 75%;
  margin: 0 0 24px 0;
  color: #394573;
}
@media (max-width: 1330px) {
  .slider__slide-description--plan {
    width: 83%;
    margin: 0 0 16px 0;
  }
}
.slider__slide-price {
  margin: 0 0 24px 0;
  font-size: 42px;
  line-height: 51px;
  font-weight: 600;
}
@media (max-width: 1330px) {
  .slider__slide-price {
    margin: 0 0 111px 0;
    font-size: 22px;
    line-height: 27px;
  }
}
.slider__slide-button {
  margin: 0 0 95px 0;
  text-decoration: none;
}
@media (max-width: 1330px) {
  .slider__slide-button {
    margin: 0 0 32px 0;
    padding: 12px 18px 10px;
  }
}
@media (max-width: 1330px) {
  .slider__slide-image {
    width: 150px;
    height: 185px;
    position: absolute;
    bottom: 5px;
    right: -16px;
    object-fit: contain;
  }
}
.slider__button {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: none;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
  color: #726DA8;
  background-color: #ffffff;
  cursor: pointer;
}
@media (max-width: 1330px) {
  .slider__button {
    display: none;
  }
}
.slider__button:disabled {
  color: #BEE7E8;
}
.slider__button--prev {
  top: 32px;
  left: 112px;
}
.slider__button--next {
  top: 32px;
  right: 112px;
}
@media (max-width: 1330px) {
  .slider__wrapper {
    position: absolute;
    bottom: 0;
    left: 45%;
    display: flex;
    justify-content: center;
  }
}
@media (max-width: 1330px) {
  .slider__dot {
    width: 16px;
    height: 16px;
    background-color: #BEE7E8;
    border-radius: 50%;
    margin: 0 8px 0 0;
    cursor: pointer;
  }
}
@media (max-width: 1330px) {
  .slider__dot--active {
    background-color: #394573;
  }
}
.main-content {
  padding: 55px 112px 40px 112px;
}
@media (max-width: 1330px) {
  .main-content {
    padding: 48px 0 48px 0;
  }
}
.content-list {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 32px;
  list-style-type: none;
}
@media (max-width: 1330px) {
  .content-list {
    display: flex;
    flex-direction: column;
    align-items: stretch;
  }
}
.content-list__item {
  height: 630px;
  padding: 0 40px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: end;
  color: #ffffff;
  background-image: url("../img/articles-background-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 40px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.25);
}
@media (min-resolution: 2dppx) {
  .content-list__item {
    background-image: url("../img/articles-background-desktop@2x.jpg");
  }
}
@media (max-width: 1330px) {
  .content-list__item {
    height: 405px;
    padding: 0 20px;
    background-image: url("../img/articles-background-mobile.jpg");
    background-position: center;
    border-radius: 24px;
  }
}
@media (max-width: 1330px) and (min-resolution: 2dppx) {
  .content-list__item {
    background-image: url("../img/articles-background-mobile@2x.jpg");
  }
}
.content-list__item:not(:last-child) {
  margin: 0 0 32px 0;
}
.content-list__item--special {
  background-image: url("../img/consultation-background-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.content-list__item-wrapper {
  margin: 0 -40px;
  padding: 0 40px;
  display: flex;
  flex-direction: column;
  background: linear-gradient(rgba(0, 0, 0, 0), #333333);
  border-bottom-left-radius: 40px;
  border-bottom-right-radius: 40px;
}
@media (max-width: 1330px) {
  .content-list__item-wrapper {
    margin: 0 -20px;
    padding: 0 20px;
    border-bottom-left-radius: 24px;
    border-bottom-right-radius: 24px;
  }
}
.content-list__title {
  margin: 0 0 16px 0;
  font-size: 32px;
  line-height: 39px;
  font-weight: 600;
}
@media (max-width: 1330px) {
  .content-list__title {
    margin: 0 0 8px 0;
    font-size: 20px;
    line-height: 24px;
  }
}
.content-list__title--special {
  width: 75%;
  margin: 0 0 35px 0;
}
.content-list__arrow {
  margin: 0 0 80px 0;
}
@media (max-width: 1330px) {
  .content-list__arrow svg {
    width: 150px;
  }
}
.content-list__description {
  margin: 0 0 27px 0;
  font-weight: 400;
}
@media (max-width: 1330px) {
  .content-list__description {
    margin: 0 0 15px 0;
    font-size: 14px;
    line-height: 17px;
  }
}
.content-list__link {
  margin: 0 -40px 0 -40px;
  padding: 22px 40px 21px 40px;
  text-decoration: none;
  color: inherit;
  font-weight: 600;
  background-color: #333333;
  border-bottom-left-radius: 40px;
  border-bottom-right-radius: 40px;
}
@media (max-width: 1330px) {
  .content-list__link {
    margin: 0 -20px 0 -20px;
    padding: 10px 20px 10px 20px;
    font-size: 16px;
    line-height: 19px;
    border-bottom-left-radius: 24px;
    border-bottom-right-radius: 24px;
  }
}
.content-list__sublist {
  margin: 0;
  padding: 0;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  list-style-type: none;
}
@media (max-width: 1330px) {
  .content-list__sublist {
    display: block;
    width: 100%;
  }
}
.content-list__item--special:hover .content-list__sublist {
  display: block;
}
.content-list__sublist-item {
  height: 582px;
  padding: 48px 40px 0 40px;
  position: relative;
  color: #333333;
  background-color: #ffffff;
  border-radius: 40px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.25);
}
@media (max-width: 1330px) {
  .content-list__sublist-item {
    height: 333px;
    padding: 32px 20px 40px 20px;
    display: flex;
    flex-direction: column;
    border-radius: 24px;
  }
}
.content-list__sublist-title {
  width: 80%;
  margin: 0 0 24px 0;
  font-size: 32px;
  line-height: 39px;
  font-weight: 600;
  color: #394573;
}
@media (max-width: 1330px) {
  .content-list__sublist-title {
    width: 100%;
    margin: 0 0 8px 0;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
  }
}
.content-list__sublist-price {
  margin: 0 0 24px 0;
  font-size: 32px;
  line-height: 39px;
  font-weight: 600;
  color: #A0D2DB;
}
@media (max-width: 1330px) {
  .content-list__sublist-price {
    margin: 0 0 10px 0;
    font-size: 26px;
    line-height: 32px;
    text-align: center;
  }
}
.content-list__sublist-list {
  margin: 0 0 57px 0;
  padding: 0;
  list-style-type: none;
  font-size: 22px;
  line-height: 27px;
  font-weight: 400;
}
@media (max-width: 1330px) {
  .content-list__sublist-list {
    margin: 0 0 30px 0;
    font-size: 14px;
    line-height: 17px;
  }
}
.content-list__sublist-list-item {
  display: flex;
  align-items: center;
}
.content-list__sublist-list-item:not(:last-child) {
  margin: 0 0 24px 0;
}
@media (max-width: 1330px) {
  .content-list__sublist-list-item:not(:last-child) {
    margin: 0 0 16px 0;
  }
}
.content-list__sublist-description {
  width: 85%;
  margin: 0;
}
.content-list__sublist-point {
  width: 56px;
  height: 56px;
  margin: 0 20px 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #BEE7E8;
  border-radius: 50%;
}
@media (max-width: 1330px) {
  .content-list__sublist-point {
    width: 32px;
    height: 32px;
    margin: 0 8px 0 0;
  }
}
@media (max-width: 1330px) {
  .content-list__sublist-button {
    font-size: 16px;
  }
}
.main-footer {
  padding: 49px 112px 46px 112px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1330px) {
  .main-footer {
    padding: 24px 0 49px 0;
    flex-direction: column;
  }
}
.main-footer__contacts {
  margin: 0;
}
.main-footer__copyright {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #394573;
}
@media (max-width: 1330px) {
  .main-footer__copyright {
    font-size: 18px;
    line-height: 22px;
    text-align: center;
  }
}
.main-footer__copyright-text {
  margin: 0;
}
@media (max-width: 1330px) {
  .main-footer__logo-wrapper {
    margin: 0 0 32px 0;
    order: -1;
  }
}
.contacts__contacts {
  display: flex;
  flex-direction: column;
}
@media (max-width: 1330px) {
  .contacts__contacts {
    margin: 0 0 32px 0;
  }
}
.contacts__contacts a {
  text-decoration: none;
  font-style: normal;
  color: #726DA8;
}
.contacts__contacts a:not(:last-child) {
  margin: 0 0 8px 0;
}
.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  z-index: 5;
  width: 100%;
  height: 100%;
