}
@media (max-width: 1330px) {
  .modal-container__close-button {
    top: 16px;
    right: 16px;
  }
}
.modal-container__close-button:hover,
.modal-container__close-button:focus,
.modal-container__close-button:active {
  opacity: 80%;
}
.modal-container__close-button-line {
  position: absolute;
  top: -2px;
  right: 11px;
  width: 2px;
  height: 28px;
  background-color: #394573;
  transform: rotate(45deg);
}
.modal-container__close-button-line::after {
