  text-align: center;
}
@media (max-width: 1330px) {
  .modal-container__notice {
    margin: 0 0 24px 0;
    font-size: 16px;
    line-height: 19px;
  }
}
.modal-container__submit-button {
  margin: 0 auto;
  padding: 17px 88px 16px 88px;
  display: block;
}
@media (max-width: 1330px) {
  .modal-container__submit-button {
    padding: 11px 63px 11px 63px;
    font-size: 20px;
    line-height: 24px;
  }
}
.form__set {
  margin: 0;
  padding: 0;
  border: none;
}
.radio-set {
  margin: 0 0 32px 0;
}
@media (max-width: 1330px) {
  .radio-set {
    margin: 0 0 18px 0;
  }
}
.radio-set__title {
  margin: 0 0 16px 0;
  font-size: 22px;
  line-height: 27px;
